.message {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin:24px 0 23px;
    font-family: 'Montserrat', sans-serif;
}

.imageAndText {
    display: flex;
    align-items: flex-end;
    max-width: 70%;
    flex-direction: row-reverse;
    font-family: 'Montserrat', sans-serif;

}

.imageAndText img {
    width: 48px;
    height: 48px;
    border-radius: 50%;

}

.text {
    position: relative;
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat', sans-serif;
    max-width: 100%;
    padding: 7px 25px 8px 13px;
    margin-right: 20px;
    background-color: #F5F7FB;
    border-radius: 10px 10px 10px 0;
}

.text:after {
    content:' ';
    position: absolute;
    width: 0;
    height: 0;
    top:auto;
    bottom: 0;
    border-left: 0 solid transparent;
    border-right: 26px solid transparent;
    border-bottom: 23px solid #F5F7FB;
    right: -15px;
    left: auto;

}

.name {
    font-size: 18px;
    line-height: 22px;
    font-family: 'Montserrat', sans-serif;
}

.messageText {
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    white-space: pre-wrap;
    word-wrap: break-word;



}

.time {
    width: 48px;
    text-align: center;
    margin-top: 5px;
    font-size: 10px;
    font-family: 'Montserrat', sans-serif;


}
